html {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: linear-gradient(#57606f, #2f3542);
}
.user-interaction-page {
  height: auto;
  padding-top: 30px;
  padding-bottom: 30px;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.select-person {
  margin-bottom: 20px;
}
.select-person label {
  color: #03e9f4;
}
.select-person select {
  margin-inline: 10px;
  border-radius: 5px;
  border: none;
  background-color: rgba(0, 0, 0, 0.17) !important;
  color: #03e9f4;
}
#persontype option {
  background-color: #2f3542 !important;
}
#persontype option:hover {
  background-color: #2f3542 !important;
}
.login-box {
  width: 330px;
  padding: 40px;
  background: rgba(0, 0, 0, 0.7);
  box-sizing: border-box;
  box-shadow: 0 15px 25px rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  margin: auto;
}

.login-box h2 {
  margin: 0 0 30px;
  padding: 0;
  color: #fff;
  text-align: center;
  font-size: 1.2rem;
}

.login-box .user-box {
  position: relative;
}

.login-box .user-box input {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  margin-bottom: 30px;
  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  background: transparent;
}
.login-box .user-box label,
.register-box .user-box label {
  position: absolute;
  top: -20px;
  left: 0;
  padding: 10px 0;
  color: #03e9f4;
  font-size: 12px;
  pointer-events: none;
  transition: 0.5s;
}

.login-box .user-box input:focus ~ label,
.login-box .user-box input:valid ~ label {
  top: -20px;
  left: 0;
  color: #03e9f4;
  font-size: 12px;
}

.login-box form a {
  position: relative;
  display: inline-block;
  padding: 10px 15px;
  color: #03e9f4 !important;
  font-size: 10px;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  transition: 0.5s;
  margin-top: 20px;
  letter-spacing: 1px;
  cursor: pointer;
}

.login-box a:hover {
  background: #03e9f4;
  color: black !important;
  border-radius: 5px;
  box-shadow: 0 0 5px #037a81, 0 0 25px #037a81, 0 0 50px #037a81,
    0 0 100px #037a81;
}
.login-box .btns-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.login-box a span {
  position: absolute;
  display: block;
}

.login-box a span:nth-child(1) {
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #03e9f4);
  animation: btn-anim1 1s linear infinite;
}

.sign-up-text {
  width: 100%;
  text-align: center;
  color: #fff;
  margin-top: 20px;
}
.sign-up-text span {
  color: #03e9f4;
  cursor: pointer;
}

@keyframes btn-anim1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}

.login-box a span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #03e9f4);
  animation: btn-anim2 1s linear infinite;
  animation-delay: 0.25s;
}

@keyframes btn-anim2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}

.login-box a span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #03e9f4);
  animation: btn-anim3 1s linear infinite;
  animation-delay: 0.5s;
}

@keyframes btn-anim3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}

.login-box a span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #03e9f4);
  animation: btn-anim4 1s linear infinite;
  animation-delay: 0.75s;
}

@keyframes btn-anim4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}
